class Friends extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			friends: null
		}
	}
	componentDidMount() {
		this.getFriends();
	}
	getFriends() {
		let url = `https://api.vk.com/method/friends.get?v=5.52&access_token=${this.props.access_token}`
		fetchJsonp(url)
			.then(res => res.json())
			.then(data => {
				this.setState({ friends: data.response.items });
				console.log(data)
			})
			.catch(e => console.error(e));
	}
	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader
					left={<HeaderButton onClick={this.props.go} data-to="home">
						{this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					Панель управления.
				</PanelHeader>
				{this.state.friends}
			</Panel>
		);
	}
}

Friends.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	access_token: PropTypes.string.isRequired
};

export default Friends;
